<template>
  <div>
    <vue-svg-tree
        :treeData="treeData"
        svgId='svg'
        ref="svgTree"
      ></vue-svg-tree>
  </div>
</template>

<script>
import VueSvgTree from 'vue-svg-tree'
import { list } from './list.js'
export default {
  name: 'HelloWorld',
  components:{
    VueSvgTree
  },
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      treeData:list
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



